<template>
    <v-list>
        <v-subheader inset>Backend Software</v-subheader>
        <v-list-tile
                v-for="(_, i) in dumbLoadingSoftware"
                :key="String(i)"
                @click="">
            <v-list-tile-content>
                <ContentLoader
                        :height="60"
                        :width="1000"
                        :speed="2"
                        primaryColor="#f3f3f3"
                        secondaryColor="#ecebeb"
                >
                    <rect x="70" y="15" rx="4" ry="4" width="117" height="6.4"/>
                    <rect x="196" y="15" rx="3" ry="3" width="85" height="6.4"/>
                    <rect x="70" y="30" rx="3" ry="3" width="350" height="6.4"/>
                    <circle cx="27.4" cy="25.4" r="17.4"/>
                </ContentLoader>
            </v-list-tile-content>
        </v-list-tile>

        <v-subheader inset>Backend Configuration</v-subheader>

        <v-list-tile
                v-for="(_, i) in dumbLoadingConfig"
                :key="String(i) + 's'"
                @click="">
            <v-list-tile-content>
                <ContentLoader
                        :height="60"
                        :width="1000"
                        :speed="2"
                        primaryColor="#f3f3f3"
                        secondaryColor="#ecebeb"
                >
                    <rect x="70" y="15" rx="4" ry="4" width="117" height="6.4"/>
                    <rect x="196" y="15" rx="3" ry="3" width="85" height="6.4"/>
                    <rect x="70" y="30" rx="3" ry="3" width="350" height="6.4"/>
                    <circle cx="27.4" cy="25.4" r="17.4"/>
                </ContentLoader>
            </v-list-tile-content>
        </v-list-tile>
    </v-list>
</template>

<script>
    import {ContentLoader} from 'vue-content-loader';

    export default {
        name: 'LoadingList',
        components: {
            ContentLoader
        },
        data() {
            return {
                dumbLoadingSoftware: Array(4),
                dumbLoadingConfig: Array(2),
            }
        }
    }
</script>

<style scoped>
</style>



