<template>
    <div>
        <v-layout row>
            <v-flex xs12 sm8 offset-sm2>
                <v-card>
                    <v-toolbar color="indigo" dark>
                        <v-toolbar-title>LiveArt prerequisites check</v-toolbar-title>

                        <v-spacer></v-spacer>

                        <v-btn icon @click="check()" :loading="loading">
                            <v-icon>refresh</v-icon>
                        </v-btn>

                    </v-toolbar>
                    <v-list v-if="loaded">
                        <v-subheader inset>Backend Software</v-subheader>
                        <v-list-tile
                            v-for="item in items.software"
                            :key="item.name"
                            avatar
                            @click=""
                        >
                            <v-list-tile-action>
                                <v-icon v-if="item.ok" color="green">check</v-icon>
                                <v-icon v-else-if="item.type === 'error'" color="red">error_outline</v-icon>
                                <v-icon v-else color="orange">warning</v-icon>
                            </v-list-tile-action>

                            <v-list-tile-content>
                                <v-list-tile-title>{{item.name}}</v-list-tile-title>
                                <v-list-tile-sub-title v-if="item.ok">
                                    {{item.installedVersion}} installed.
                                </v-list-tile-sub-title>
                                <v-list-tile-sub-title v-else-if="item.installedVersion === 'none'">
                                    <b>{{"Not installed: "}}</b> {{item.errorText || null}}
                                </v-list-tile-sub-title>
                                <v-list-tile-sub-title v-else>Installed version: <b>{{item.installedVersion}}</b>,
                                    expected:
                                    <b>{{item.expectedRange}}</b>
                                </v-list-tile-sub-title>
                            </v-list-tile-content>
                            <v-list-tile-avatar>
                                <a :href="item.url" target="_blank">
                                    <v-icon v-if="!item.ok">open_in_new</v-icon>
                                </a>
                            </v-list-tile-avatar>
                        </v-list-tile>

                        <v-subheader inset>Backend Configuration</v-subheader>
                        <v-list-tile
                            avatar
                            @click=""
                        >
                            <v-list-tile-action>
                                <v-icon v-if="designsConfig.writable" color="green">check</v-icon>
                                <v-icon v-else color="red">error_outline</v-icon>
                            </v-list-tile-action>

                            <v-list-tile-content>
                                <v-list-tile-title>Designs folder</v-list-tile-title>
                                <v-list-tile-sub-title v-if="designsConfig.writable">"{{designsConfig.path}}" is
                                    writable.
                                </v-list-tile-sub-title>
                                <v-list-tile-sub-title v-else>"{{designsConfig.path}}" is <b>not writable</b>.
                                </v-list-tile-sub-title>
                            </v-list-tile-content>
                            <v-list-tile-avatar>
                            </v-list-tile-avatar>
                        </v-list-tile>

                        <v-list-tile
                            avatar
                            @click=""
                        >
                            <v-list-tile-action>
                                <v-icon v-if="uploadConfig.writable || uploadConfig.destination === 'aws'"
                                        color="green">
                                    check
                                </v-icon>
                                <v-icon v-else color="red">error_outline</v-icon>
                            </v-list-tile-action>

                            <v-list-tile-content>
                                <v-list-tile-title>Image upload configuration</v-list-tile-title>
                                <v-list-tile-sub-title
                                    v-if="uploadConfig.writable && uploadConfig.destination === 'folder'">
                                    Destination:
                                    "folder", "{{uploadConfig.path}}" is writable. Max upload size:
                                    {{uploadConfig.maxSize}}.
                                </v-list-tile-sub-title>
                                <v-list-tile-sub-title
                                    v-else-if="!uploadConfig.writable && uploadConfig.destination === 'folder'">
                                    Destination: "folder", "{{uploadConfig.path}}" is <b>not writable</b>.
                                </v-list-tile-sub-title>
                                <v-list-tile-sub-title v-else>Destination: "{{uploadConfig.destination}}". Max upload
                                    size: {{uploadConfig.maxSize}}.
                                </v-list-tile-sub-title>
                            </v-list-tile-content>
                            <v-list-tile-avatar>
                                <v-btn flat icon color="rgba(0,0,0,.54)" @click="showUploadInfo()">
                                    <v-icon>info_outline</v-icon>
                                </v-btn>
                            </v-list-tile-avatar>
                        </v-list-tile>

                    </v-list>
                    <LoadingList v-else/>
                </v-card>
            </v-flex>
        </v-layout>

        <v-dialog
            v-model="uploadDialog"
            max-width="600"
        >
            <v-card>
                <v-card-title class="headline">Upload destination</v-card-title>
                <v-card-text>
                    <p style="text-align: left;">
                        Upload destination may be configured in "services/php/configs.php" by modifying
                        <code>$UPLOAD_CONFIGS</code>
                        object. <br/><br/>
                        The destination may be configured to be "folder", in which case the uploaded images will be
                        stored
                        locally, or "aws", in which case the images will get uploaded to S3 AWS. <br/><br/>


                        <small>* AWS destination will require a configured bucket.</small>
                    </p>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="primary"
                        flat
                        @click="uploadDialog = false"
                    >
                        OK
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog
            v-model="initError"
            persistent
            max-width="600"
        >
            <v-card>
                <v-card-title class="headline">Server problem</v-card-title>
                <v-card-text>
                    <p style="text-align: left;">
                        It seems that a webserver is not listening on this file or PHP is not enabled.
                        <br/><br/>
                        Please make sure:<br/>
                        1. There is a webserver (Apache, Nginx etc) running and serving the root LiveArt folder;<br/>
                        2. The PHP is enabled. <br/>
                        3. You are accessing this file through a web URL (like <i>http://localhost:9000/requirements-check/client/</i>)
                        and not by just clicking on "index.html" file.
                    </p>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="primary"
                        flat
                        :loading="loading"
                        @click="check()"
                    >
                        RETRY
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog
            v-model="loadingErrorDialog"
            width="500"
        >
            <v-card>
                <v-card-title
                    class="subheading grey lighten-2"
                    primary-title
                >
                    Service communication error
                </v-card-title>

                <v-card-text>
                    Code status: "{{loadingErrorText}}"
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="blue darken-2"
                        flat
                        :loading="loading"
                        @click="check()"
                    >
                        RETRY
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    const semver = require('semver');
    import {ListLoader} from 'vue-content-loader';
    import LoadingList from "./LoadingList.vue";

    export default {
        name: 'CheckView',
        components: {
            ListLoader,
            LoadingList
        },
        data() {
            return {
                checkResult: {
                    software: [],
                    configuration: []
                },
                loading: false,
                loadingErrorDialog: false,
                loadingErrorText: "",
                loaded: false,
                dumbLoadingSoftware: Array(4),
                uploadDialog: false,
                initError: false
            }
        },
        methods: {
            check() {
                if (this.loading) {
                    return;
                }
                this.loading = true;
                this.loaded = false;
                this.$http.get("../../requirements-check/services/php/check.php")
                    .then(response => {
                        this.checkResult = response.body;
                        this.loading = false;
                        this.loaded = true;
                        this.initError = false;
                    }, response => {
                        if (response.status === 0) {
                            this.initError = true;
                        } else if (
                            !response.ok
                        ) {
                            this.loadingErrorText = response.statusText;
                            this.loadingErrorDialog = true;
                        }
                    })
                    .catch(e => {
                        console.warn(e);
                        alert(e.message);
                    })
                    .finally(() => {
                        this.loading = false;
                    });

            },
            showUploadInfo() {
                this.uploadDialog = true;
            },
            removeLeadingZeros(versionStr) {
                try {
                    const regexp = /^[v=]?(\d+)\.(\d+)\.?(\d*)(.*)/;
                    const res = regexp.exec(versionStr);
                    // according to SemVer specs
                    const X = res[1];
                    const Y = res[2];
                    const Z = res[3];
                    const metadata = res[4];

                    let ver = "";
                    ver += `${Number(X)}.${Number(Y)}`;

                    if (!!Z && Z.length) {
                        ver += `.${Number(Z)}`;
                    }

                    ver += metadata;

                    return ver;
                } catch (e) {
                    console.warn(e);
                    return null;
                }
            }
        },
        mounted() {
            this.check();
        },
        computed: {
            items() {
                return {
                    software: this.checkResult.software.map(r => {
                        let installedVersion = r.installedVersion;

                        if (r.installedVersion !== "none") {
                            let parsedVer = semver.coerce(r.installedVersion);

                            if (!parsedVer) {
                                const noLeadingZerosVer = this.removeLeadingZeros(r.installedVersion);
                                parsedVer = semver.coerce(noLeadingZerosVer);
                            }

                            if (parsedVer) {
                                installedVersion = parsedVer.version;
                            } else {
                                installedVersion = "could not detect";
                            }
                        }

                        return {
                            ...r,
                            ok: r.installedVersion === true || (r.installedVersion !== "none" && !!semver.coerce(installedVersion) && semver.satisfies(installedVersion, r.expectedRange)),
                            installedVersion
                        };
                    })
                };
            },
            uploadConfig() {
                return this.checkResult.configuration.upload;
            },
            designsConfig() {
                return this.checkResult.configuration.designs;
            }
        }
    }
</script>

<style scoped>
    a {
        color: #fff;
        text-decoration: none;
    }
</style>



